import { BriefStepsLegend, VerticalScrollStep } from "@rp/react/web/common/composite-components"
import { Box, Typography } from "@rp/react/web/mui/core"
import { CheckCircle } from "@rp/react/web/mui/icons"

type BriefCompletedProps = {
  isEdit: boolean
}

const BriefCompleted = ({ isEdit }: BriefCompletedProps) => {
  const text = isEdit
    ? "Your Brief has been successfully edited!"
    : "Your Brief has been successfully created!"

  return (
    <VerticalScrollStep>
      <BriefStepsLegend sx={{ alignItems: "flex-start" }}>
        <Box sx={{ display: "flex", alignItems: "center", gap: 1, mb: 1 }}>
          <Typography sx={{ fontWeight: 700, fontSize: 18 }}>{text}</Typography>
          <CheckCircle color="success" />
        </Box>
        <Typography>You will receive notifications whenever there are updates.</Typography>
      </BriefStepsLegend>
    </VerticalScrollStep>
  )
}

export { BriefCompleted }
